export default {
    actions:{
        setIsThemeDark(ctx, val){
            ctx.commit('setIsThemeDark', val)
        }
    },
    mutations:{
        setIsThemeDark(state, isThemeDark) {
            state.isThemeDark = isThemeDark;
        }
    },
    store:{isThemeDark:false},
    getters:{
        isThemeDark:state=>state.isThemeDark
    }
}